function randomIntFromInterval(min, max) {
  // min and max included
  return Math.floor(Math.random() * (max - min + 1) + min);
}

function getDistanceAndAngleBetweenTwoPoints(point1, point2) {
  const x = point2.x - point1.x;
  const y = point2.y - point1.y;

  return {
    distance: Math.sqrt(x * x + y * y),
    angle: (Math.atan2(y, x) * 180) / Math.PI
  };
}

export function generateUniverse(options) {
  console.log();
  console.log("GENERATION STARTED");
  const start = performance.now();
  const {
    maxStarGenLoops,
    maxStars,
    edgeDistance,
    size,
    minimumStarDistance,
    maxGenTime
  } = options;
  const center = {
    x: size.width / 2,
    y: size.height / 2
  };
  const maxDistanceFromCenter = size.width / 2;
  // const maxDistanceFromCenter = Math.sqrt(
  //   center.x * center.x + center.y * center.y
  // );
  console.log();
  console.log("Center", center);
  console.log("Max distance from center", maxDistanceFromCenter);
  let starGenLoops = 0;
  let starSprites = [];
  // for (let i = 0; i < maxStars; i++) {
  while (
    starSprites.length < maxStars &&
    starGenLoops < maxStarGenLoops &&
    performance.now() - start < maxGenTime
  ) {
    // console.log("while");
    let starCoordinate = {
      x: randomIntFromInterval(edgeDistance, size.width - edgeDistance),
      y: randomIntFromInterval(edgeDistance, size.height - edgeDistance)
    };

    const distanceFromCenter = getDistanceAndAngleBetweenTwoPoints(
      starCoordinate,
      center
    ).distance;
    const percentOfMaxDistance = distanceFromCenter / maxDistanceFromCenter;
    const minDistance = minimumStarDistance * percentOfMaxDistance;

    if (percentOfMaxDistance > 1 || percentOfMaxDistance < 0.01) {
      starCoordinate = null;
    } else if (starSprites.length === 0) {
      starCoordinate = center;
    } else {
      // check all existing stars to see if they are close
      for (let s = 0; s < starSprites.length; s++) {
        // console.log("for");
        if (starSprites.length > 0) {
          const coordinateToTest = starSprites[s];
          const coordinateDistance = getDistanceAndAngleBetweenTwoPoints(
            starCoordinate,
            coordinateToTest
          ).distance;

          if (coordinateDistance < minDistance) {
            // console.log("too close");
            starGenLoops++;
            starCoordinate = null;
            break;
          }
        }
      }
    }
    if (starCoordinate !== null) {
      starSprites.push(starCoordinate);
    }
  }
  console.log(starSprites);
  if (starGenLoops >= maxStarGenLoops) {
    console.log(
      `GENERATION HALTED: Hit loop limit of ${maxStarGenLoops.toLocaleString()}`
    );
  }
  if (starSprites.length >= maxStars) {
    console.log(
      `GENERATION HALTED: Hit star limit of ${maxStars.toLocaleString()}`
    );
  }
  if (performance.now() - start >= maxGenTime) {
    console.log(
      `GENERATION HALTED: Hit max gen time of ${maxGenTime.toLocaleString()}ms`
    );
  }

  const universe = {
    stars: starSprites,
    size,
    genSpeed: (performance.now() - start).toLocaleString(),
    genLoops: starGenLoops
  };
  return universe;
}
